





































































































img {
  max-width: 100%;
}
.choice {
  background-color: #ebf4fb;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  border: 1px solid #d6dfe6;
  transition: outline 0.1s, box-shadow 0.1s;
  outline: 0 solid #efdd0d;
}
.title {
  margin: 0;
  word-break: break-all;
  font-size: 2em;
  width: 100%;
}
input, select {
  margin-bottom: 1em;
}
.imgSource {
  align-self: left;
}
.description {
  width: 100%;
  height: 10em;
}
.horizontal {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.horizontal > * {
  margin: 0.5em 0;
}
.imageMsg {
  margin: 1em;
  font-style: italic;
}

