



































































































@import '../mixins/ui.scss';
.choiceGroupFlatView.top {
  // background-color: #dbecf9;
  border: 10px solid rgba(11, 129, 214, 0.5);
  padding: 2em;
}
.choiceGroup {
  //padding-left: 0px;
  text-align: initial;
  padding-left: 0;
  margin: 0;
} 
.node {
  list-style-type: none;
  max-width: 100%;
  background-color: #0080e414;
  padding: 1em;
  border: 1px solid rgba(11, 129, 214, 0.6);
} 
.node:not(:first-child) {
  margin-top: -1px;
}
.sortable-chosen:not(.sortable-ghost) {
  color: transparent;

}
.sortable-chosen:not(.sortable-ghost) * {
  opacity: 0;
  outline:  solid 10px;
}

.sortable-ghost {
  color: #fff;
  background-color: #0081e4;
}
ul {
  padding: 0;
}
.options {
  display: flex;
  margin: 1em 0;
}
.options > * {
  flex: 1 1 auto;
  margin: 5px 10px;
  background-color: rgba(87, 192, 52, 0.251);
  font-weight: normal;
  padding: 0.4em;
  border: none;
}
.options > *:first-child {
  margin-left: 0;
}
.options > *:last-child {
  margin-right: 0;
}
.title {
  display: flex;
  justify-content: space-between;
}
.delete {
  padding: 0.3em 0.6em;
  font-size: 0.8em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: rgba(251, 18, 18, 0.341);
  font-weight: bold;
  margin-left: 2em;
}
h1 {
  font-size: 1.2em;
  font-weight: normal;
  margin: 0;
}
h1.groupTitle {
  font-weight: bold;
}
.node.group {
  padding-bottom: 0;
}
.dragArea {
  min-height: 3.5em;
  margin: 0 1em;
  padding: 1em 0;
}

