









































































































.choiceGroupFlatView.top {
  background-color: #dbecf9;
  padding: 2em;
}
ul {
  text-align: initial;
  padding-left: 0;
  margin: 0;
}  
li {
  list-style-type: none;
  margin: 10px 30px;
  max-width: 100%;
}
li.choiceGroup {
  flex: 0 1 auto;
}
h1 {
  font-size: 1.2em;
  font-weight: normal;
  margin: 0;
}
h1.groupTitle {
  font-weight: bold;
}
header {
  display: flex;
  align-items: baseline;
  flex-wrap: wrap;
}
.qtyButtons {
  user-select: none; // stop accidental selection when clicking qty increments
}
.maxSelectable, .minSelectable, .price, span.qty, .qtyButtons {
  margin-left: 1em;
}
.maxSelectable, .minSelectable, .price, span.qty {
  font-style: italic;
  color: rgb(128, 128, 128);
}
.price.gain::before {
  content: 'Gain:';
  color: #217f21;
}
.price.cost::before {
  content: 'Cost:';
  color: #842323;
}
.disabled {
  opacity: 0.3;
  pointer-events: none;
}
.selected {
  position: relative;
}
.selected::before {
  color: #decd55;
  content: "\2022";
  position: absolute;
  left: -0.7em;
  font-size: 3em;
  line-height: 0.6em;
}

